*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.app{
    background-color: #15202B;
    font-family: 'Poppins', sans-serif;
    color: #fff;
}

.btn{
    background-color: #1568A0;
}
.title{
    color: #1568A0;
}
ion-icon{
    color: #1568A0;
    font-size: 30px;
    margin-left: 15px;
    margin-top: 5px;
    cursor: pointer;
}



/* navbar */

.navbar-nav a{
    margin-left: 5px;
}

.navbar-nav a:hover{
    color:  #1568A0;
}

.navbar-toggler{
    background: #1568A0;
}
.navbar-toggler-icon{
    border-radius: 5px;
    font-size: 15px;
}



/* home */
#home h1{
    font-size: 30px;
    margin-top: 40px;
    color: #1568A0;
}

#home h2, span{
    font-size: 40px;
    font-weight: 700;
    color: #1568A0;
}

.home-icon ion-icon{
    color: aliceblue;
    font-size: 30px;
    margin-left: 15px;
    margin-top: 5px;
    cursor: pointer;
}

.home-icon ion-icon:hover{
    color: #1568A0;
}



form{
   margin: auto;
   width: 90%;
   display: flex;
   flex-direction: column;
   border-radius: 30px;
}

/* portfolio */

.portfolio-content{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
}


/* contact */

.contact{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
 }
.contact-cards{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}



/* footer */

footer .icon{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    color: #1568A0;
    font-size: 30px;
    cursor: pointer;
}


@media(max-width:550px){
    .portfolio-content{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: 20px;
        margin: 20px;
    }
    
    .contact{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .contact-cards{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin: 10px;  
    }

   
    footer .icon{
        display: none;
    }
    footer p{
        text-align: center;
    }
}

